___CSS_LOADER_EXPORT___.push([module.id, ".tr-selected{background-color:rgba(0,0,255,.2);border:1px dashed #444;color:blue;font-weight:600}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Inventario/Components/AreasAsignadasComponent.vue"],"names":[],"mappings":"AAmQA,aACA,iCAAA,CACA,sBAAA,CACA,UAAA,CACA,eACA","sourcesContent":["<template>\r\n    <v-dialog v-model=\"dialog\" fullscreen scrollable>\r\n        <template v-slot:activator=\"{ on, attrs }\">\r\n            <v-btn color=\"primary\" block dark v-bind=\"attrs\" v-on=\"on\">\r\n                Areas Asignadas\r\n            </v-btn>\r\n        </template>\r\n\r\n        <v-card tile>\r\n            <v-toolbar>\r\n                <v-app-bar-nav-icon @click=\"dialog = false\">\r\n                    <v-icon>mdi-arrow-left</v-icon>\r\n                </v-app-bar-nav-icon>\r\n\r\n                <v-toolbar-title> Areas Asignadas</v-toolbar-title>\r\n            </v-toolbar>\r\n\r\n            <v-card-text style=\"height: 90vh\">\r\n                <v-autocomplete\r\n                    v-model=\"area_selected\"\r\n                    :items=\"areas\"\r\n                    item-text=\"nombre\"\r\n                    item-value=\"id\"\r\n                    label=\"Seleccione un Area\"\r\n                    class=\"my-4\"\r\n                    outlined\r\n                    dense\r\n                ></v-autocomplete>\r\n\r\n                <v-card :loading=\"loading_table\">\r\n                    <v-overlay absolute :value=\"loading_table\">\r\n                        <v-progress-circular\r\n                            indeterminate\r\n                            size=\"64\"\r\n                        ></v-progress-circular>\r\n                    </v-overlay>\r\n                    <v-card-title>\r\n                        <v-row>\r\n                            <v-col cols=\"12\" sm=\"5\" class=\"pb-1\">\r\n                                <v-combobox\r\n                                    v-model=\"mostrar_selected\"\r\n                                    :items=\"items_combobox\"\r\n                                    label=\"Mostrar\"\r\n                                    outlined\r\n                                    hide-details\r\n                                    dense\r\n                                ></v-combobox>\r\n                            </v-col>\r\n                            <v-col cols=\"12\" sm=\"7\" class=\"pb-1\">\r\n                                <v-text-field\r\n                                    v-model=\"area_search\"\r\n                                    append-icon=\"mdi-magnify\"\r\n                                    label=\"Buscar\"\r\n                                    hide-details\r\n                                    outlined\r\n                                    dense\r\n                                ></v-text-field>\r\n                            </v-col>\r\n                        </v-row>\r\n                    </v-card-title>\r\n                    <v-simple-table>\r\n                        <template v-slot:default>\r\n                            <thead class=\"grey lighten-3\">\r\n                                <tr>\r\n                                    <th class=\"text-left\">Estado</th>\r\n                                    <th class=\"text-left\">Descripcion</th>\r\n                                    <th class=\"text-left\">Codigo</th>\r\n                                    <th class=\"text-left\">Cod. SIGA</th>\r\n                                </tr>\r\n                            </thead>\r\n                            <tbody>\r\n                                <tr\r\n                                    v-for=\"(item, index) in bienes_result\"\r\n                                    :key=\"index\"\r\n                                    :class=\"[\r\n                                        index == tr_index ? 'tr-selected' : '',\r\n                                    ]\"\r\n                                    @click=\"onSelectColum(item, index)\"\r\n                                    @dblclick=\"onSelectColumDobleClik(item)\"\r\n                                >\r\n                                    <td>\r\n                                        <template v-if=\"item.registrado == 1\">\r\n                                            <v-chip\r\n                                                small\r\n                                                color=\"grey\"\r\n                                                outlined\r\n                                                class=\"ma-2\"\r\n                                            >\r\n                                                <small>REGISTRADO</small>\r\n                                            </v-chip>\r\n                                        </template>\r\n                                        <template v-else>\r\n                                            <v-chip\r\n                                                small\r\n                                                color=\"orange\"\r\n                                                outlined\r\n                                                class=\"ma-2\"\r\n                                            >\r\n                                                <small>NO REGISTRADO</small>\r\n                                            </v-chip>\r\n                                        </template>\r\n                                    </td>\r\n                                    <td>{{ item.descripcion }}</td>\r\n                                    <td>{{ item.codigo }}</td>\r\n                                    <td>\r\n                                        {{ item.codigo_siga }}\r\n                                    </td>\r\n                                </tr>\r\n                            </tbody>\r\n                        </template>\r\n                    </v-simple-table>\r\n\r\n                    <v-card-actions>\r\n                        <v-spacer></v-spacer>\r\n                        <v-pagination\r\n                            v-model=\"page\"\r\n                            class=\"\"\r\n                            :length=\"pages\"\r\n                            :total-visible=\"5\"\r\n                        ></v-pagination>\r\n                        <v-spacer></v-spacer>\r\n                    </v-card-actions>\r\n                </v-card>\r\n            </v-card-text>\r\n            <v-divider></v-divider>\r\n\r\n            <v-card-actions>\r\n                <h5>Total: {{ total_result }}</h5>\r\n                <v-spacer></v-spacer>\r\n                <v-btn color=\"primary\" @click=\"SeleccionarBien()\">\r\n                    <v-icon left>mdi-check</v-icon> Seleccionar\r\n                </v-btn>\r\n            </v-card-actions>\r\n        </v-card>\r\n    </v-dialog>\r\n</template>\r\n<script>\r\nimport axios from \"axios\";\r\n\r\nexport default {\r\n    props: {\r\n        areas: Array,\r\n    },\r\n    data: () => ({\r\n        loading_table: false,\r\n\r\n        tr_index: null,\r\n        tr_item: {},\r\n\r\n        items_combobox: [\"Todos\", \"Registrados\", \"Sin registrar\"],\r\n\r\n        dialog: false,\r\n        area_selected: null,\r\n        search: \"\",\r\n\r\n        bienes_result: [],\r\n        mostrar_selected: \"Todos\",\r\n        area_search: \"\",\r\n\r\n        page: 1,\r\n        total_result: 0,\r\n        pages: 1,\r\n    }),\r\n    methods: {\r\n        onSelectColum(item, index) {\r\n            this.tr_index = index;\r\n            this.tr_item = item;\r\n        },\r\n        onSelectColumDobleClik(item) {\r\n\r\n            item.registrado = item.registrado == 1 ? true : false;\r\n            this.$emit(\"setData\", item);\r\n            this.dialog = false;\r\n\r\n            this.bienes_result = [];\r\n            this.area_selected = null;\r\n        },\r\n\r\n        async getBienes(area, term = \"\", mostrar = \"Todos\", page = 1) {\r\n            let res = await axios.post(\"/inventario/get-bienes?page=\" + page, {\r\n                area: area,\r\n                mostrar: mostrar,\r\n                term: term,\r\n            });\r\n\r\n            this.page = res.data.datos.current_page;\r\n            this.total_result = res.data.datos.total;\r\n            this.pages = res.data.datos.last_page;\r\n\r\n            return res.data.datos.data;\r\n        },\r\n\r\n        SeleccionarBien() {\r\n            tr_item.registrado = tr_item.registrado == 1 ? true : false;\r\n            this.$emit(\"setData\", this.tr_item);\r\n            this.dialog = false;\r\n            this.bienes_result = [];\r\n            this.area_selected = null;\r\n        },\r\n    },\r\n\r\n    watch: {\r\n        async area_selected(val) {\r\n            if (!val) return;\r\n\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes(val);\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n\r\n        async area_search(val) {\r\n            //if (!val) return;\r\n\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes(\r\n                this.area_selected,\r\n                val,\r\n                this.mostrar_selected\r\n            );\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n\r\n        async page(val, old_val) {\r\n            if (val == old_val) return;\r\n\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n\r\n            let res = await this.getBienes(\r\n                this.area_selected,\r\n                this.area_search,\r\n                this.mostrar_selected,\r\n                val\r\n            );\r\n            this.bienes_result = res;\r\n\r\n            this.loading_table = false;\r\n        },\r\n\r\n        async mostrar_selected(val) {\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes(\r\n                this.area_selected,\r\n                this.area_search,\r\n                val\r\n            );\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style>\r\n.tr-selected {\r\n    background-color: rgba(0, 0, 255, 0.2);\r\n    border: 1px dashed #444;\r\n    color: blue;\r\n    font-weight: 600;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
